import {combineReducers, compose,applyMiddleware} from 'redux';
import foundationReducers from './foundationReducers';

import Thunk from 'redux-thunk';
import testingReducers from './testingReducers';
import cbChecklistReducers from "./cbChecklistReducers";

const allReducers=Object.assign({},foundationReducers,testingReducers,cbChecklistReducers);
compose(applyMiddleware(Thunk));
export default combineReducers(allReducers);
