import {connect} from "react-redux";
import {loadNewMod, makeAppTopMod} from "../../utilities/utilityFunctions";
import store from "../../store";
import {useEffect} from "react";
import {updateAppSettings, updateDataStore} from "../../actions/foundationActions";

const KimWatcher = (props) => {
    let makeTopFunc = props.makeTopFunc ? props.makeTopFunc : makeAppTopMod;
    let stateData = store.getState();
    useEffect(()=>{
        if(stateData.appSettings.routedComponent && stateData.appSettings.routedComponent !== "" ){
            makeTopFunc(stateData.appSettings.routedComponent);
        }
        else if(stateData.appSettings.defaultModule){
            makeTopFunc(stateData.appSettings.defaultModule);
        }
        //eslint-disable-next-line
    },[stateData.appSettings.defaultModule])

    useEffect(()=>{
        if(stateData.appSettings.appMainModuleName){
            loadNewMod(stateData.appSettings.appMainModuleName,(mod)=>store.dispatch(updateDataStore({appMainModule:mod.default})));
        }
    },[stateData.appSettings.appMainModuleName])

    useEffect(()=>{
        store.dispatch(updateAppSettings({apiURL:process.env.REACT_APP_API_URL}))
    },[])
    return <div style={{display:"none"}}>KimWatcher</div>
}

const mapStateToProps = (state) =>{
    return {
        appSettings:state.appSettings,
        dataStore:state.dataStore,
        tabGroups:state.tabGroups,
        tabStore:state.tabStore,
        testing:state.testing
    };
};

export default connect(mapStateToProps,{})(KimWatcher);