import {appSettingModel} from "../datamodels/foundation";

const siteModelChecklistReducer = (state={},action) => {
    if (action.type === 'UPDATE_SITE_MODEL_CHECKLIST') {
        return  Object.assign({}, state, action.payload);
    }
    return state;
};

let exp = {
    siteModelChecklist:siteModelChecklistReducer
};

export default exp;